@import "./variables.scss";

.cart {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: space-between;

  &__counter {
    display: block;
    position: absolute;
    top: -9px;
    right: -10px;
    padding: 2px 6px;
    font-size: 12px;
    color: $white;
    background-color: $blue;
    border-radius: 4px;
  }
}

.button.button--cart {
  margin-right: 6px;

  &:hover {
    background-color: inherit;

    img {
      animation: sway 1s 0.1s ease-in-out;
    }
  }
}

.button.button--add-to-cart {
  position: fixed;
  bottom: 24px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  // gap: 8px;
  width: 264px;
  height: 45px;
  padding: 12px 20px;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: 0.5px;
  background-color: $blue;
  border-color: $blue;
  color: $white;
  z-index: 100;

  img {
    position: absolute;
    right: -10%;
    top: 10%;
  }

  &:hover {
    background-color: darken($color: $blue, $amount: 10);
  }

  &:active {
    transform: scale(0.9);
  }
}

.button--in-cart {
  img {
    transform: translateX(-20px);
  }
}

.button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes sway {
  0% {
    -webkit-transform: rotateZ(0);
  }
  20% {
    -webkit-transform: rotateZ(8deg);
  }
  40% {
    -webkit-transform: rotateZ(-6deg);
  }
  60% {
    -webkit-transform: rotateZ(7deg);
  }
  80% {
    -webkit-transform: rotateZ(-9deg);
  }
}

@keyframes cart {
  33% {
    transform: translateX(0px);
  }

  66% {
    transform: translateX(-2px);
  }

  100% {
    transform: translateX(0px);
  }
}

.cart-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;

  &__title {
    font-size: 14px;
  }

  th,
  td {
    font-size: 14px;
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
}

.cart-table 

// .cart-table th {
//   background-color: #f2f2f2;
// }

.cart-actions {
  text-align: right;
  margin-top: 20px;
}

.button-text {
  display: inline-block;
  transition: opacity 300ms ease-in-out;
}

.button-text-enter {
  opacity: 0;
}

.button-text-enter-active {
  opacity: 1;
}

.button-text-exit {
  opacity: 1;
}

.button-text-exit-active {
  opacity: 0;
}

/* Для React Transition Group */
.button-text {
  position: relative;
}

.cart-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  margin: 0;
  padding: 0;
  text-align: left;
  list-style: none;

  &__title {
    font-size: 14px;
    font-weight: 500;
    color: $blue;
    text-decoration: none;
  }

  &__item {
    display: flex;
    gap: 8px;
    width: 100%;
    position: relative;
    padding: 12px;
    padding-right: 24px;
    background-color: $white;
    // border: 2px solid $blue;
    border-radius: 8px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  }

  &__img-wrapper,
  &__info-game {
    flex-basis: 50%;
  }

  &__img-wrapper {
    img {
      max-width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 4px;
    }
  }

  &__info-game {
    // flex-basis: 75%;
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  &__edition,
  &__platforms {
    font-size: 14px;
    color: $darkgrey;
  }

  &__price {
    margin: 0;
    margin-top: auto;
    font-weight: 500;
  }

  &__message {
    padding: 6px 12px;
    background-color: lighten($color: $orange, $amount: 40);
    border-radius: 8px;
  }

  &__delete {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -10px;
    right: -10px;
    width: 24px;
    height: 24px;
    padding: 0;
    font-size: 16px;
    border: none;
    border-radius: 50%;
    color: $white;
    background-color: darken($color: $blue, $amount: 10);

    &::after {
      display: inline-block;
      content: "\00d7"; /* This will render the 'X' */
      font-size: 24px;
    }

    &:hover {
      background-color: darken($color: $blue, $amount: 20);
    }
  }
}

.button.button--checkout {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  // gap: 8px;
  // width: 264px;
  // height: 65px;
  width: 100%;
  max-width: 500px;
  padding: 12px 20px;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: 0.5px;
  background-color: $blue;
  border-color: $blue;
  color: $white;

  &:hover {
    &:hover {
      background-color: darken($color: $blue, $amount: 10);
    }

    &:active {
      transform: scale(0.9);
    }
  }
}

.button .arrow {
  margin-right: 1rem;
  height: 0.1rem;
  width: 2.5rem;

  position: relative;
  bottom: 8px;
  left: 16px;
  background: $white;

  transition: width 0.2s ease;
}

.button .arrow::before,
.button .arrow::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 0;

  background: $white;

  height: 0.1rem;
  width: 0.6rem;
}

.button .arrow::before {
  transform: rotate(45deg);
  transform-origin: right bottom;
}

.button .arrow::after {
  transform: rotate(-45deg);
  transform-origin: right top;
}

.button:hover .arrow {
  width: 4rem;
}
