@import "./variables.scss";
@import "./mixins.scss";

.input {
  padding: 10px 8px;
  font-size: 14px;
  border: 1px solid $black;
  border-radius: 4px;

  &--search {
    flex-grow: 1;
  }
}

.search-bar {
  display: flex;
  justify-content: center;
  gap: 8px;
  flex-grow: 1;
  max-width: 350px;
  margin-left: auto;
  // margin-top: 24px;
  // margin-bottom: 16px;
  padding-left: 16px;
  // padding-right: 16px;

  @include for-tablet-portrait {
    width: 60%;
    // margin-bottom: 48px;
    padding: 0;
  }

  @include for-tablet-landscape {
    width: 50%;
  }

  @include for-big-desktop {
    width: 35%;
  }
}

.search__empty {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 80vh;

  img {
    margin-bottom: 16px;

    @include for-desktop {
      width: 160px;
    }
  }

  p {
    margin: 0;
  }
}