@import "./variables.scss";
@import "./mixins.scss";

.game-card {
  flex-basis: 60%;
  flex-grow: 0;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  cursor: pointer;

  &-link {
    display: block;
    text-decoration: none;
    color: inherit;
  }

  img {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12px;
  }

  &__info {
    // margin-top: 6px;
    padding-left: 4px;
  }

  .game-cover-wrapper {
    position: relative;
    width: 100%;
    border: 4px solid transparent;
    border-radius: 16px;
    transition: border 0.1s linear;
  }

  &:hover .game-cover-wrapper {
    border-color: $blue;
    filter: brightness(85%);
  }

  @media (min-width: 1200px) {
    flex-basis: 25%;
  }

  &--inner-page {
    flex-basis: calc(50% - 6px);

    @include for-tablet-portrait {
      flex-basis: calc(33.3333% - 8px);
    }

    @include for-tablet-landscape {
      flex-basis: calc(25% - 10px);
    }

    @include for-big-desktop {
      flex-basis: calc(20% - 10px);
    }
  }

  &--sub {
    flex-basis: calc(33.3333% - 12px);
    margin-right: 0;
    margin-bottom: 24px;
    border: none;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &--selected {
    .game-cover-wrapper {
      border-color: $blue;
    }
  }

  &--edition {
    flex-basis: 70%;
    margin-right: 24px;
    border: 4px solid transparent;
    border-radius: 16px;
    transition: border-color 0.1s linear;
    cursor: pointer;

    &.selected {
      background-color: lighten($color: $blue, $amount: 40);
      border-color: $blue;
    }

    img {
      height: 100%;
      object-fit: cover;
    }

    .game-cover-wrapper {
      border: none;
    }

    @media (min-width: 1200px) {
      flex-basis: 50%;
    }
  }
}
