@import "./variables.scss";

.button {
  padding: 6px 10px;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  color: $black;
  background: transparent;
  border-radius: 4px;
  border: 1px solid $black;
  cursor: pointer;
  transition: all 0.1s linear;

  &:hover {
    color: $white;
    background: $black;
    border-color: $black;
  }

  &[disabled] {
    color: $grey;
    border-color: $grey;
  }

  &--load-more {
    margin-top: 16px;
  }

  &--back {
    position: absolute;
  }

  &--inner-game-page {
    top: 16px;
    left: 16px;
    background: $black;
    color: $white;
    border-color: $black;

    // &:hover {
    //   color: $black;
    //   background: transparent;
    //   border-color: $black;
    // }
  }

  &--duration {
    padding: 16px 24px;
    font-size: 16px;
    border: 1px solid $grey;
    border-radius: 0;
    border-right: 0;
    cursor: pointer;

    &:first-child {
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
    }

    &:last-child {
      border-right: 1px solid $grey;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
    }

    &.selected {
      background-color: $blue;
      color: $white;
      border-color: $blue;
    }
  }

  &--translate {
    margin-top: 16px;
  }

  &--search {
    padding: 10px 20px;
    font-size: 14px;
    border-radius: 4px;
    cursor: pointer;
  }

  &--delete {
    background-color: $red;
    color: $white;
    border-color: transparent;

    &:hover {
      background-color: darken($color: $red, $amount: 10);
    }
  }

  &.admin__tab {
    &:hover {
      color: inherit;
      background: inherit;
      border-color: transparent;
    }

    &--active {
      &:hover {
        border-color: $blue;
      }
    }
  }

  &--main {
    background-color: $blue;
    color: $white;
    border-color: $blue;

    &:hover {
      background-color: darken($color: $blue, $amount: 10);
      border-color: $blue;
    }
  }
}

.button.form__button.form__button--sub:hover {
  color: inherit;
  background: inherit;
  border: 1px solid $blue;
}

.button.form__button.form__button--sub.form__button--selected:hover {
  color: $white;
  background: $blue;
}