@import "./variables.scss";

.form {
  margin-bottom: 16px;
  text-align: left;

  &--subs {
    .form__wrapper {
      margin-bottom: 0;
    }
  }

  &__input {
    width: 100%;
    margin-bottom: 12px;
  }

  &__select {
    cursor: pointer;
  }

  &--add-game {
    .form__wrapper {
      margin-bottom: 24px;
    }
  }

  &__label {
    display: block;
    margin: 0;
    font-size: 14px;
  }

  &__textarea {
    height: 150px;
    resize: none;
  }

  &__button {
    &--selected {
      color: $white;
      background-color: $blue;
      border: $blue !important;
    }
  }

  &__buttons {
    display: flex;
    gap: 16px;
    margin-top: 24px;
  }

  .button {
    // margin-left: 12px;
    padding: 11px 10px;
  }

  &__fieldset {
    margin-bottom: 8px;
    padding: 16px;
    border-radius: 8px;
  }

  &__advice {
    margin: 0;
    margin-top: -10px;
    margin-bottom: 12px;
    font-size: 12px;
  }

  &__message {
    padding: 16px;
    border-radius: 5px;
    text-align: center;

    &--success {
      background-color: #d4edda;
      color: $green;
      border: 1px solid #c3e6cb;
    }

    &--warning {
      background-color: #fff3cd;
      color: $orange;
      border: 1px solid #ffeeba;
    }

    &--error {
      background-color: #f8d7da;
      color: $red;
      border: 1px solid #f5c6cb;
    }
  }

  &__subscriptions-list {
    display: flex;
    margin: 0 auto;
    margin-bottom: 16px;

    .button {
      flex-basis: 33.3333%;
      padding: 18px 16px;
      border: 1px solid $grey;
      border-radius: 0;
      border-right: 0;

      &:first-child {
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
      }

      &:last-child {
        border-right: 1px solid $grey;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
      }
    }
  }

  &__filters {
    display: flex;
    gap: 8px;
  }

  &__durations {
    display: flex;
    justify-content: space-between;
    gap: 8px;

    .form__wrapper {
      flex-basis: 33.3333%;
    }
  }

  &__hr {
    margin: 16px 0;
  }

  &__draggable {
    margin-bottom: 8px;
    padding: 16px 24px;
    background-color: lighten($color: $blue, $amount: 35);
    border-radius: 8px;
    cursor: grab;

    &:hover {
      color: $white;
      background-color: $blue;

      .button:not(.form__button--delete) {
        // color: $white;
        // border-color: $white;
      }
    }
  }

  &__collection-item {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
}

.admin {
  &__title {
    font-size: 24px;
    text-align: center;
  }

  &__tabs {
    display: flex;
    margin: 0 auto;
    margin-bottom: 24px;
  }

  &__tab {
    flex-basis: 50%;
    // padding: 8px 8px;
    padding: 0;
    padding-bottom: 8px;
    font-size: 16px;
    background-color: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 2px solid transparent;
    cursor: pointer;

    &--active {
      border-bottom-color: $blue;
    }
  }

  @media (min-width: 1200px) {
    max-width: 700px;
    margin: 0 auto;
  }
}

.form__games-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form__game-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 8px;
  border-bottom: 1px solid $grey;

  .form__button--edit {
    flex-grow: 1;
    font-size: 14px;
    text-align: left;
    padding-left: 0;
    border: none;

    &:hover {
      color: inherit;
      background: inherit;
    }
  }

  .form__button--selected {
    font-weight: 600;
    color: $black;
    background-color: transparent;
  }
}

.form__button--delete {
  margin-left: auto;
  background-color: $red;
  color: $white;
  border-color: transparent;
}

.form__button-remove {
  padding: 8px 10px !important;
  margin-left: 8px;
}

.form__button--delete:hover {
  background-color: darken($color: $red, $amount: 10);
}

.form--games {
  flex-basis: 100%;
  margin-top: 20px;
  margin-bottom: 24px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination-button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  background-color: #fff;
  cursor: pointer;
}

.pagination-button.active {
  background-color: #007bff;
  color: #fff;
  border-color: #007bff;
}

textarea {
  resize: none;
}

.form__button-add-collection {
  margin-bottom: 12px;
}

.form__button-confirm {
  margin-bottom: 12px;
  margin-right: 8px;
}