@import "./mixins.scss";

.games-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 16px;

  &--horizontal {
    overflow-x: scroll;
  }

  &--vertical {
    flex-wrap: wrap;
    gap: 12px;

    .game-title {
      font-size: 14px;
    }
  }

  &--wrapper {
    flex-wrap: nowrap;

    .game-card {
      flex-basis: calc(60% - 16px);
      // margin-right: 0;

      .game-cover-wrapper {
        flex-grow: 1;
      }
    }
  }

  &-section {
    padding: 16px;
    padding-right: 0;

    @include for-tablet-portrait {
      margin-bottom: 36px;
      padding: 0;
    }

    @include for-big-desktop {
      margin-bottom: 48px;
    }
  }

  &-header {
    display: flex;
    align-items: baseline;
    gap: 8px;
    margin-bottom: 12px;
    margin-top: 12px;
    
    @include for-tablet-portrait {
      margin-top: 36px;
    }

    @include for-tablet-portrait {
      margin-bottom: 24px;
    }
  }

  &-title {
    margin: 0;
    text-align: left;
    font-size: 18px;
    font-weight: 600;

    &--inner-page {
      flex-grow: 1;
      margin-bottom: 0;
      text-align: center;

      @include for-tablet-portrait {
        padding: 24px 0;
      }
    }
  }

  &--inner {
    margin-top: 32px;
    padding-right: 16px;

    @include for-tablet-portrait {
      margin-top: 0;
    }

    .game-card {
      margin: 0;
    }

    .game-title {
      @include for-desktop {
        font-size: 16px;
      }
    }
  }

  &--sub {
    justify-content: center;
    gap: 8px;
    max-width: 400px;
    margin: 0 auto;
    overflow: auto;
  }
}

