@import "./variables.scss";

.game {
  &-title {
    margin: 0;
    padding: 0;
    font-size: 16px;
  }

  &-type {
    font-size: 12px;
    color: $grey;
  }

  &-platforms {
    // position: absolute;
    // bottom: 16px;
    // left: 12px;
    padding: 4px 8px;
    font-size: 10px;
    color: $white;
    background: rgba($black, 0.6);
    border-radius: 4px;

    &__wrapper {
      display: flex;
      gap: 4px;
      position: absolute;
      bottom: 16px;
      left: 12px;
    }
  }

  &-price {
    display: flex;
    gap: 4px;
    align-items: center;
    margin: 0;
    padding: 0;
  }

  &-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
    margin-bottom: 12px;
  }

  &-tag {
    background-color: $lightgrey;
    padding: 5px 10px;
    border-radius: 15px;
    font-size: 10px;
    cursor: default;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #d0d0d0;
    }
  }

  &-edition {
    &-info {
      padding: 8px;
      padding-left: 12px;

      .game-title {
        font-size: 16px;
      }
    }

    &-features {
      margin: 0;
      padding: 0;
      padding-left: 16px;

      &-title {
        margin-bottom: 8px;
      }
    }
  }
}
