@import "./variables.scss";
@import "./mixins.scss";

.game-details {
  display: flex;
  flex-direction: column;

  &-info {
    width: 100%;
    margin-top: -24px;
    padding: 16px;
    padding-top: 0;
    padding-bottom: 64px;
    text-align: left;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    background: $white;

    @include for-desktop {
      margin-top: -64px;
      padding: 24px;
    }
  }

  &-title {
    margin-bottom: 0;
    font-size: 20px;
    font-weight: 600;

    @include for-desktop {
      margin-top: 0;
    }
  }

  &-price {
    display: flex;
    gap: 4px;
    margin-top: 0;
    font-size: 20px;
    font-weight: 600;
  }

  &-discount {
    padding: 4px 10px;
    font-size: 14px;
    font-weight: 700;
    background-color: $orange;
    border-radius: 16px;
  }

  &-cover {
    min-height: 25vh;
    max-height: 25vh;
    max-width: 100%;
    object-fit: cover;

    @include for-phone {
      min-height: 30vh;
      max-height: 30vh;
    }

    @include for-big-phone {
      min-height: 40vh;
      max-height: 40vh;
    }

    @include for-tablet-portrait {
      min-height: 50vh;
      max-height: 50vh;
    }

    @include for-desktop {
      min-height: 70vh;
      max-height: 70vh;
    }

    @include for-big-desktop {
      object-position: right 25%;
    }

    &--sub {
      min-height: 50vh;
      max-height: 50vh;
      object-position: right 15%;

      @include for-big-phone {
        min-height: 65vh;
        max-height: 65vh;
      }

      @include for-desktop {
        min-height: 70vh;
        max-height: 70vh;
        object-position: right 5%;
      }
    }
  }

  &-genre,
  &-platforms,
  &-voice,
  &-subtitles,
  &-releasedate {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 14px;
    font-weight: 500;

    span:first-child {
      @include for-desktop {
        flex-basis: 15%;
      }
    }

    @include for-desktop {
      justify-content: flex-start;
    }
  }

  &-description {
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;

    span:first-child {
      color: $grey;
    }

    &-text {
      max-width: 700px;
    }
  }

  &-quote {
    max-width: 700px;
    margin: 0 auto;
    margin-top: 16px;
    padding: 0;
    padding-left: 8px;
    font-size: 14px;
    border-left: 2px solid $blue;
  }

  &-duration-selector {
    display: flex;
    justify-content: center;
  }

  &-addons {
    .game-edition-info {
      padding: 0;
    }
  }
}

.game-discount {
  padding: 4px 6px;
  font-size: 12px;
}

/* Класс для анимации изображения */
.game-details-cover {
  transition: opacity 300ms ease-in-out;
  /* Плавное изменение прозрачности */
}

/* Класс для скрытия изображения */
.fade-out {
  opacity: 0;
  pointer-events: none;
  /* Отключаем взаимодействие с изображением во время анимации */
}

details {
  font-size: 14px;
}

details > summary:after {
  content: " (окрыть)";
}

details[open] > summary:after {
  content: " (закрыть)";
}

// Анимация смены обложки игры

.game-details-cover-wrapper {
  position: relative;
  z-index: -100;
}

.game-details-cover {
  transition: opacity 0.3s ease-in-out;
}

.game-details-cover.fade-out {
  opacity: 0;
}

.game-details-cover.fade-in {
  opacity: 1;
}

.game-details-cover-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.game-details-cover-wrapper.fade-out::before {
  opacity: 1;
}

.game-details-cover--sub {
  transition: opacity 0.3s ease-in-out;
}

.game-details-cover--sub.fade-out {
  opacity: 0;
}

.game-details-cover--sub.fade-in {
  opacity: 1;
}

.game-details-cover-wrapper--sub {
  position: relative;
}

.game-details-cover-wrapper--sub::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.game-details-cover-wrapper--sub.fade-out::before {
  opacity: 1;
}

.game-details-description-text {
  white-space: pre-line;
}
