@import "./variables.scss";
@import "./mixins.scss";

.slick-arrow {

  &:hover {

    &::before {
      color: darken($color: $blue, $amount: 10);
      transition: color 0.1s linear;
    }
  }
}

.slick-prev,
.slick-next {
  z-index: 1000;
}

.slick-prev,
.slick-next {
  top: 40%;
  // transform: translateY(-50%);

  @include for-tablet-portrait {
    top: 33%;
  }

  @include for-big-desktop {
    top: 37%;
  }
}

.slick-prev {
  left: -22px;
}

.slick-next {
  right: 22px;
}

.slick-prev:before,
.slick-next:before {
  font-size: 48px;
  color: $blue;
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0;
}

.slick-track {
  display: flex;
  // align-items: stretch;
  gap: 12px;
  margin-left: 0;

  .slick-slide:first-child {
    margin-left: -12px;
  }
}

.game-details-editions .slick-track {
  gap: 8px;
}

.slick-dots li.slick-active button:before {
  color: $blue;
}

.slick-dots li button:before {
  color: $grey;
}

.slick-slide:first-child {
  margin-left: -16px;
}

.game-details-editions .slick-slide:first-child {
  margin-left: -8px;
}